<template>
  <view class="container">
    <!-- ÊêúÁ¥¢Ê°Ü -->
    <view class="search-section">
      <view class="search-bar">
        <view class="search-input-container">
          <input 
            v-model="searchQuery" 
            placeholder="ËæìÂÖ•‰∏≠ÊñáÊàñÊÑèÂ§ßÂà©ËØ≠ÂçïËØç..." 
            class="search-input"
            @input="onSearchInput"
            confirm-type="search"
            @confirm="performSearch"
          />
          <button class="search-btn" @click="performSearch">
            <text class="search-icon">üîç</text>
          </button>
        </view>
      </view>
      
      <!-- ÊêúÁ¥¢ÊèêÁ§∫ -->
      <view class="search-tips" v-if="!searchQuery">
        <text class="tip-text">üí° ÊîØÊåÅ‰∏≠ÊñáÂíåÊÑèÂ§ßÂà©ËØ≠ÊêúÁ¥¢</text>
      </view>
    </view>
    
    <!-- ÊêúÁ¥¢ÁªìÊûú -->
    <view class="results-section" v-if="searchResults.length > 0">
      <view class="results-header">
        <text class="results-title">ÊêúÁ¥¢ÁªìÊûú</text>
        <text class="results-count">ÂÖ±{{searchResults.length}}Êù°</text>
      </view>
      
      <view class="results-list">
        <view 
          v-for="word in searchResults" 
          :key="word.id"
          class="word-item"
          @click="goToWordDetail(word)"
        >
          <view class="word-main">
            <view class="word-text">{{ word.word }}</view>
            <view class="word-pos">{{ posMap[word.pos] }}</view>
          </view>
          <view class="word-meaning">{{ word.meaning }}</view>
          <view class="word-status">
            <view class="status-dot" :class="{ active: isWordLearned(word.id) }"></view>
            <text class="status-text">{{ isWordLearned(word.id) ? 'Â∑≤Â≠¶' : 'Êú™Â≠¶' }}</text>
          </view>
        </view>
      </view>
    </view>
    
    <!-- Êó†ÊêúÁ¥¢ÁªìÊûú -->
    <view class="no-results" v-if="hasSearched && searchResults.length === 0">
      <view class="no-results-icon">üîç</view>
      <view class="no-results-title">Êú™ÊâæÂà∞Áõ∏ÂÖ≥ÂçïËØç</view>
      <view class="no-results-desc">ËØ∑Â∞ùËØïÂÖ∂‰ªñÊêúÁ¥¢ËØçÊ±á</view>
    </view>
    
    <!-- ÊêúÁ¥¢ÂéÜÂè≤ -->
    <view class="history-section" v-if="!hasSearched && searchHistory.length > 0">
      <view class="history-header">
        <text class="history-title">ÊêúÁ¥¢ÂéÜÂè≤</text>
        <button class="clear-history-btn" @click="clearHistory">Ê∏ÖÁ©∫</button>
      </view>
      <view class="history-list">
        <view 
          v-for="(item, index) in searchHistory" 
          :key="index"
          class="history-item"
          @click="searchFromHistory(item)"
        >
          <text class="history-text">{{ item }}</text>
        </view>
      </view>
    </view>
    
    <!-- ÁÉ≠Èó®ËØçÊ±áÊé®Ëçê -->
    <view class="recommend-section" v-if="!hasSearched && searchHistory.length === 0">
      <view class="recommend-header">
        <text class="recommend-title">Êé®ËçêËØçÊ±á</text>
      </view>
      <view class="recommend-list">
        <view 
          v-for="word in recommendWords" 
          :key="word.id"
          class="recommend-item"
          @click="goToWordDetail(word)"
        >
          <view class="recommend-word">{{ word.word }}</view>
          <view class="recommend-meaning">{{ word.meaning }}</view>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
import { 
  getCurrentBookWords, 
  getCurrentBookLearnedWords,
  posMap
} from '@/utils/bookData.js'

export default {
  data() {
    return {
      searchQuery: '',
      searchResults: [],
      hasSearched: false,
      searchHistory: [],
      recommendWords: [],
      allWords: [],
      learnedWords: [],
      posMap
    }
  },
  onLoad() {
    this.loadData()
    this.loadSearchHistory()
    this.loadRecommendWords()
  },
  methods: {
    loadData() {
      this.allWords = getCurrentBookWords()
      this.learnedWords = getCurrentBookLearnedWords()
    },
    
    loadSearchHistory() {
      try {
        const history = uni.getStorageSync('search_history') || []
        this.searchHistory = history.slice(0, 10) // ÊúÄÂ§öÊòæÁ§∫10Êù°ÂéÜÂè≤ËÆ∞ÂΩï
      } catch (e) {
        this.searchHistory = []
      }
    },
    
    loadRecommendWords() {
      // ÈöèÊú∫Êé®Ëçê‰∏Ä‰∫õÂçïËØç
      const shuffled = [...this.allWords].sort(() => 0.5 - Math.random())
      this.recommendWords = shuffled.slice(0, 20)
    },
    
    onSearchInput() {
      // ÂÆûÊó∂ÊêúÁ¥¢
      if (this.searchQuery.trim()) {
        this.performSearch()
      } else {
        this.searchResults = []
        this.hasSearched = false
      }
    },
    
    performSearch() {
      const query = this.searchQuery.trim()
      if (!query) {
        this.searchResults = []
        this.hasSearched = false
        return
      }
      
      this.hasSearched = true
      
      // ÊêúÁ¥¢ÈÄªËæëÔºöÂêåÊó∂ÊêúÁ¥¢ÊÑèÂ§ßÂà©ËØ≠ÂçïËØçÂíå‰∏≠ÊñáÊÑèÊÄù
      const results = this.allWords.filter(word => {
        const wordMatch = word.word.toLowerCase().includes(query.toLowerCase())
        const meaningMatch = word.meaning.includes(query)
        return wordMatch || meaningMatch
      })
      
      // ÊåâÁõ∏ÂÖ≥Â∫¶ÊéíÂ∫èÔºàÁ≤æÁ°ÆÂåπÈÖç‰ºòÂÖàÔºâ
      this.searchResults = results.sort((a, b) => {
        const aWordExact = a.word.toLowerCase() === query.toLowerCase()
        const bWordExact = b.word.toLowerCase() === query.toLowerCase()
        const aMeaningExact = a.meaning === query
        const bMeaningExact = b.meaning === query
        
        if (aWordExact && !bWordExact) return -1
        if (!aWordExact && bWordExact) return 1
        if (aMeaningExact && !bMeaningExact) return -1
        if (!aMeaningExact && bMeaningExact) return 1
        
        return 0
      })
      
      // ‰øùÂ≠òÊêúÁ¥¢ÂéÜÂè≤
      this.saveSearchHistory(query)
    },
    
    saveSearchHistory(query) {
      try {
        let history = uni.getStorageSync('search_history') || []
        
        // ÁßªÈô§ÈáçÂ§çÈ°π
        history = history.filter(item => item !== query)
        
        // Ê∑ªÂä†Âà∞ÂºÄÂ§¥
        history.unshift(query)
        
        // ÈôêÂà∂ÂéÜÂè≤ËÆ∞ÂΩïÊï∞Èáè
        history = history.slice(0, 50)
        
        uni.setStorageSync('search_history', history)
        this.searchHistory = history.slice(0, 10)
      } catch (e) {
        console.error('‰øùÂ≠òÊêúÁ¥¢ÂéÜÂè≤Â§±Ë¥•:', e)
      }
    },
    
    searchFromHistory(query) {
      this.searchQuery = query
      this.performSearch()
    },
    
    clearHistory() {
      uni.showModal({
        title: 'Á°ÆËÆ§',
        content: 'Á°ÆÂÆöË¶ÅÊ∏ÖÁ©∫ÊêúÁ¥¢ÂéÜÂè≤ÂêóÔºü',
        success: (res) => {
          if (res.confirm) {
            try {
              uni.removeStorageSync('search_history')
              this.searchHistory = []
              uni.showToast({
                title: 'Â∑≤Ê∏ÖÁ©∫ÂéÜÂè≤ËÆ∞ÂΩï',
                icon: 'success'
              })
            } catch (e) {
              console.error('Ê∏ÖÁ©∫ÂéÜÂè≤ËÆ∞ÂΩïÂ§±Ë¥•:', e)
            }
          }
        }
      })
    },
    
    isWordLearned(wordId) {
      return this.learnedWords.some(word => word.id === wordId)
    },
    
    goToWordDetail(word) {
      uni.navigateTo({
        url: `/pages/wordDetail/wordDetail?wordId=${word.id}&fromPage=search`
      })
    }
  }
}
</script>

<style scoped>
