  background: rgba(255, 255, 255, 0.95);
  border-radius: 20rpx;
  padding: 20rpx;
  backdrop-filter: blur(10px);
  box-shadow: 0 4rpx 16rpx rgba(0, 0, 0, 0.1);
}

.search-input-container {
  display: flex;
  align-items: center;
  gap: 20rpx;
}

